<template>
  <div>
    <v-sheet color="blue darken-4" elevation="1" height="150">
      <v-row class="fill-height text-center" align="center" justify="center">
        <v-col cols="11" md="6">
          <div class="text-h5 text-md-h3 white--text font-weight-bold mb-2">
            Knowledge Base
          </div>
        </v-col>
      </v-row>
    </v-sheet>
    <v-container class="mt-2">
      <v-row justify="end" class="mb-2">
        <v-col cols="12" md="6">
          <v-text-field
            ref="search"
            label="Search a keyword"
            v-model="search"
            single-line
            solo
            rounded
            clearable
            append-icon="mdi-magnify"
            hide-details
            @keyup.enter="getRecords()"
            @click:append="getRecords()"
            :disabled="loadingRecords"
          >
          </v-text-field>
        </v-col>
      </v-row>

      <v-list-item three-line v-for="item in records" :key="item.id">
        <v-list-item-content>
          <v-list-item-title>{{ item.keyword }}</v-list-item-title>
          <v-list-item-subtitle>
            {{ item.remarks }}
          </v-list-item-subtitle>
          <v-divider></v-divider>
        </v-list-item-content>
      </v-list-item>
    </v-container>
    <Footer />
  </div>
</template>

<script>
import Footer from "@/components/Footer.vue";
import { mapState } from "vuex";

export default {
  name: "KnowledgeBase",

  components: { Footer },

  data: () => ({
    search: null,
  }),

  computed: {
    ...mapState("knowledgeBase", ["records", "loadingRecords"]),
  },

  methods: {
    async getRecords() {
      await this.$store.dispatch("knowledgeBase/getRecords", this.search);
      this.$refs.search.focus();
    },
  },
};
</script>
